@import 'libs/normalize';
@import 'variables';
@import 'mixins';

@import './components/header';


.page {
  padding: 0 15px;
  height: 100vh;
  background-color: $almostWhite;
  margin: 0 auto;
  max-width: 1400px;

  @include onPhoneBig {
    width: calc(100vw - 100px);
    padding: 0;
  }
}

.content {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 50px;

  @include onPhoneSmall {
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
    justify-content: center;
  }

  @include onTab {
    grid-template-columns: repeat(2, 1fr);
  }
}

.main {
  &__information {
    margin: 0 auto;
    align-self: center;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    grid-row: 2;

    @include onTab {
      grid-row: 1;
    }
  }

  &__articule {
    display: block;
    word-wrap: normal;
    font-size: 30px;
    font-weight: 700;
    text-align: center;

    margin-bottom: 20px;
    
    @include onPhoneBig {
      font-size: 45px;
      margin-bottom: 40px;
    }

    @include onDestop {
      font-size: 65px;
      text-align: left;
    }
  }

  &__text {
    color: $mediumGray;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    text-align: center;

    margin-bottom: 20px;

    @include onPhoneBig {
      margin-bottom: 40px;
    }

    @include onDestop {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__button {
    display: block;
    width: max-content;
    padding: 15px 30px;
    background-color: $almostBlack;
    border: 1px solid $almostBlack;
    border-radius: 10px;
    font-size: 18px;
    color: $almostWhite;
    cursor: pointer;
    transition: $duration;

    &:hover {
      background-color: $almostWhite;
      color: $almostBlack;
    }

    @include onPhoneSmall {
      margin: 0 auto;
      margin-bottom: 20px;
    }

    @include onTab {
      margin: 0;
    }
  }
  &__block-text {
    margin-top: 15%;

    @include onDestop {
      margin-top: 30%;
    }
  }

  &__block-client {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;

    &__img {
      width: 70px;

      @include onTab {
        width: 90px;
      }
    }
  }

  &__block-img {
    justify-self: center;
  }

  &__img {
    width: 100%;

    @include onPhoneBig {
      width: 400px;
    }

    @include onBigScreen {
      width: 500px;
    }
  }
}
