@import '../mixins';
@import '../variables';

.header {
  margin-bottom: 20px;

  @include onTab {
    margin-bottom: 50px;
  }

  &__block {
    display: inherit;
    align-items: start;
    position: relative;
  }

  &__block-button {
    display: none;

    @include onTab {
      display: inherit;
    }
  }

  &__button {
    padding: 10px 20px;
    color: $mediumGray;
    border-radius: 10px;
    font-size: 14px;
    transition: $duration;
    margin-right: 5px;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      background-color: $mediumGray;
      color: $almostWhite;
    }

    &:active {
      background-color: black;
    }

    &-border {
      border: 1px solid $mediumGray;
    }
  }

  &__menu {
    @include onTab {
      display: none;
    }

    &-icon {
      cursor: pointer;
    }
  }
}

.logo {
  margin-right: 50px;
}

.container {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 20px 0;

  @include onDestop {
    padding: 30px;
  }
}

.nav {
  display: none;
  font-size: 14px;

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }

  @include onTab {
    display: inherit;
  }

  &__link {
    display: inline-block;
    font-size: inherit;
    color: $mediumGray;
    transition: $duration;

    cursor: pointer;

    &--active {
      margin-right: 5px;
    }

    &:hover {
      color: $almostBlack;
    }
  }
}

.arrow {
  margin-bottom: 2px;

  transition: $duration;

  &--active {
    transform: rotate(180deg);
  }
}

.add-block {
  position: absolute;
  background-color: #fff;
  padding: 20px;
  width: max-content;
  height: max-content;
  border-radius: 10px;
  box-shadow: 0 0 10px 1px $lightGray;
}

.features {
  font-size: 14px;

  &--block {
    top: 30px;
    left: 90px;
  }

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__img {
    width: 12px;
    margin-right: 10px;
  }

  &__link {
    display: block;
    color: $mediumGray;
  }

  &--menu {
    padding: 20px 0 0px 20px;
    font-size: 16px;
  }
}

.company {
  &--block {
    top: 30px;
    left: 250px;
  }
}

.wrapper {
  margin: 0 auto;
}

.menu {
  display: flex;
  justify-content: flex-end;

  position: fixed;
  margin-top: -20px;
  margin-left: -20px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);

  @include onPhoneBig {
    margin-left: -50px;
  }

  @include onTab {
    display: none;

  }

  &__content {
    width: 200px;
    height: 100vh;
    padding: 15px 20px;

    display: flex;
    flex-direction: column;
    background-color: #fff;
  }

  &__icon {
    width: 25px;
    align-self: flex-end;
    cursor: pointer;
    margin-bottom: 20px;
  }

  &--list {
    display: flex;
    flex-direction: column;
    font-size: 16px;
  }

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}